kground-color: $gray;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  position: relative;\n\n  &::after {\n    position: absolute;\n    content: '';\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    background-color: #fff;\n    opacity: 0;\n    transition: opacity 0.1s;\n  }\n\n  &:hover {\n    &::after {\n      opacity: 0.2;\n    }\n  }\n\n  &:active,\n  &:active:hover {\n    &::after {\n      opacity: 0.2;\n      background-color: #000;\n    }\n  }\n\n  &--block {\n    display: flex;\n    width: 100%;\n  }\n\n  &--primary {\n    color: #fff;\n    background: $primary-gradient;\n    transition: background 0.1s;\n  }\n\n  &--center {\n    margin: auto;\n  }\n\n  &--loading {\n    color: transparent;\n    &::before {\n      box-sizing: border-box;\n      content: '';\n      display: block;\n      position: absolute;\n      top: 50%;\n      left: 50%;\n      width: 24px;\n      height: 24px;\n      margin-top: -12px;\n      margin-left: -12px;\n      border-radius: 50%;\n      border: 2px solid rgba(255, 255, 255, 0.2);\n      border-top-color: #fff;\n      animation: spinner 0.6s linear infinite;\n\n      @keyframes spinner {\n        to {\n          transform: rotate(360deg);\n        }\n      }\n    }\n  }\n}\n",".container {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding: 24px 0 64px 0;\n  min-height: 100vh;\n}\n",".form {\n  &__item {\n    margin: 16px 0px;\n\n    &:last-child {\n      margin-bottom: 0px;\n    }\n\n    &:first-child {\n      margin-top: 0px;\n    }\n  }\n\n  &__title {\n    margin: 0 0 32px 0;\n    text-align: center;\n    font-size: 60px;\n    background: $primary-gradient;\n    -webkit-background-clip: text;\n    -webkit-text-fill-color: transparent;\n    line-height: 1.2;\n    position: relative;\n    user-select: none;\n  }\n}\n",".input {\n  display: block;\n  width: 100%;\n  height: $control-height;\n  border-radius: $border-radius;\n  border: $control-border-width solid $gray;\n  background-color: transparent;\n  padding: $control-padding;\n  font-size: $control-font-size;\n  line-height: 1;\n\n  box-shadow: 0 0 0 0px rgba($primary, 0.2);\n  transition: box-shadow 0.1s, border-color 0.1s;\n\n  &:not(:focus):hover {\n    border-color: lighten(desaturate($primary, 20%), 30%);\n  }\n\n  &:focus {\n    border-color: lighten(desaturate($primary, 10%), 10%);\n    box-shadow: 0 0 0 6px rgba($primary, 0.2);\n  }\n\n  &--icon {\n    padding-left: 40px;\n  }\n\n  &__wrapper {\n    position: relative;\n  }\n\n  &__icon {\n    position: absolute;\n    width: 22px;\n    height: 22px;\n    top: 50%;\n    transform: translateY(-50%);\n    left: 12px;\n    color: $gray--dark;\n  }\n}\n",".successMessage {\n  text-align: center;\n  margin-bottom: 24px;\n}\n",".text {\n  &--center {\n    text-align: center;\n  }\n}\n",".widget {\n  padding: 42px;\n  background-color: #fff;\n\n  @include media('small') {\n    width: 360px;\n    margin: auto;\n    border-radius: 16px;\n    box-shadow: 0 32px 172px darken(rgba(37, 200, 208, 0.2), 10%);\n  }\n}\n"],"sourceRoot":""}]);
